import React, { Component } from 'react';
import shortid from 'shortid'
import Polls from './conponents/data/Polls';
import LeftSidebar from './conponents/LeftSidebar';
import RightSidebar from './conponents/RightSidebar';

export default class SmartPoll extends Component {
	state = {
		polls: [],
    selectedPoll: {},
    searchTerm: ''
	};

	componentDidMount() {
		this.setState({ polls: Polls });
  }
  
  setSelected = id => {
    const { polls } = this.state 
    const selectedPoll = polls.find(poll => poll.id === id) 
    this.setState({selectedPoll})
  }

  getOpinion = (name, opinion) => {
    const { polls } = this.state 
    const selectedPoll = polls.find(poll => poll.id === this.state.selectedPoll.id)
    selectedPoll.opinions.push({
    id: shortid.generate(), name:name,voted:opinion
    })
    selectedPoll.totalVote++
    const voted = selectedPoll.options.find(option => option.value === opinion)
    voted.vote++ 
    this.setState({polls})
  }

  createPoll = (poll) => {
    poll.id = shortid.generate()
    poll.created = new Date()
    poll.opinions = []
    poll.totalVote = 0
    this.setState({polls:this.state.polls.concat(poll)})
  }

  updatePoll = updatedPoll => {
    const { polls } = this.state 
    const poll = polls.find(poll => poll.id === this.state.selectedPoll.id) 
    poll.title = updatedPoll.title 
    poll.description = updatedPoll.description 
    poll.options = updatedPoll.options
    this.setState({polls})
  }

  deletePoll = id => { 
    this.setState({ polls:this.state.polls.filter(poll=>poll.id!==id), selectedPoll: {}})
  }

  setSearchTerm = (e) => {
    this.setState({searchTerm:e.target.value})
  }

  searchResults = () => {
    return this.state.polls.filter(poll=>poll.title.toLowerCase().includes(this.state.searchTerm.toLowerCase()))
  }

  render() {   
    const polls = this.searchResults()
		return (
			<div style={{ width: '100%', height: '100vh', overflowY: 'scroll', background: '#100e17', color: '#ffffff' }} >
				<div className="col-11 bg-dark mx-auto p-4">
					<div className="row">
						<div className="col-4">
              <LeftSidebar
                polls={polls}
                setSelected={this.setSelected}  
                createPoll={this.createPoll}
                searchTerm={this.state.searchTerm}
                setSearchTerm={this.setSearchTerm}
              />
						</div>
						<div className="col-8">
              <RightSidebar
                updatePoll={this.updatePoll}
                selectedPoll={this.state.selectedPoll}
                getOpinion={this.getOpinion}
                deletePoll={this.deletePoll}
              />
						</div>
					</div>
				</div>
			</div>
		);
	}
}
