import React from 'react';
import shortid from 'shortid';
import Loading from './Loading';

class FormModal extends React.Component {
	state = {
		title: '',
		description: '',
		options: [
			{ id: shortid.generate(), value: '', vote: 0 },
			{ id: shortid.generate(), value: '', vote: 0 },
    ],
    errors: {}, 
    isLoading: false
  };
  
  componentDidMount() {
    const { selectedPoll } = this.props 
    if (selectedPoll && Object.keys(selectedPoll).length > 0) {
      this.setState({
        title: selectedPoll.title,
        description: selectedPoll.description,
        options : selectedPoll.options
      })
    }
  }

	changeHandler = (e) => {
		this.setState({
			[e.target.id]: e.target.value,
    }); 
	};

	optionChangeHandler = (value, index) => {
		const { options } = this.state;
		options[index].value = value;
    this.setState({ options }); 
	};

	createOption = () => {
		if (this.state.options.length < 5) {
			this.setState({
				options: this.state.options.concat({ id: shortid.generate(), value: '', vote: 0 }),
			});
		}
	};

	deleteOption = (index) => {
		const { options } = this.state;
		options.splice(index, 1);
		this.setState({ options });
	};

	submit = (e) => {
    e.preventDefault();
    const { title, description, options } = this.state 
    const poll = {
      title,description,options
    }
    const { errors, isValid } = this.errorsHandler()
    if (isValid) {
      this.setState({ isLoading: true })
      setTimeout(() => {
        this.setState({isLoading: false})
        this.props.submit(poll);
        this.setState({
          title: '',
          description: '',
          options: [
            { id: shortid.generate(), value: '', vote: 0 },
            { id: shortid.generate(), value: '', vote: 0 },
          ],
          errors: {}
      });
      e.target.reset();
      this.props.toggleModal()
      },800)
    } else {
    this.setState({ errors })
  }
  }; 

  errorsHandler = () => {
    const errors = {}
    const { title, description, options } = this.state 
    if (!title) {
      errors.title = 'Please provide title!'
    }else if (title.length > 0 && title.split(" ").length < 2) {
      errors.title = 'title too short, min 2 words required!'
    } 
    if (!description) {
      errors.description = 'Please provide description'
    } 
    const optionsErrors = [] 
    options.forEach((option,index) => {
      if (!option.value) {
        optionsErrors[index] = 'empty! please fill this option'
      }
    })
    if (optionsErrors.length > 0) {
      errors.options = optionsErrors
    }

    return {
      errors,
      isValid:Object.keys(errors).length === 0
    }
  }

  render() {
    const { title, description, options, errors } = this.state  
		return (
			<form onSubmit={this.submit}>
				<input
					onChange={this.changeHandler}
					value={title}
					id="title"
					type="text"
					placeholder="poll title..."
					className={`form-control form-control-sm ${!title || errors.title ? 'is-invalid':'is-valid'}`}
        />
        { !title || errors.title ? <small className="invalid-feedback mt-0">{errors.title}</small> : ''}
				<textarea
					onChange={this.changeHandler}
					value={description}
					id="description"
					className={`form-control form-control-sm mt-1 ${!description ? 'is-invalid':'is-valid'}`}
					placeholder="poll description..."
				></textarea>
        { !description && <small className="invalid-feedback mt-0">{errors.description}</small>}
				<button
					onClick={this.createOption}
					type="button"
					className="btn btn-success btn-sm mt-2"
					disabled={options.length === 5}
				>
					Add Option
				</button>
        {options.map((option, index) => (
        <div key={option.id}>
					<div key={index} className="mt-1 d-flex">
						<input
							onChange={(e) => this.optionChangeHandler(e.target.value, index)}
							value={option.value}
							type="text"
							placeholder="option name..."
							className={`form-control form-control-sm d-block ${!options[index].value ? 'is-invalid':'is-valid'}`}
            />
						<button
							onClick={() => this.deleteOption(index)}
							type="button"
							className="btn btn-danger btn-sm ml-2"
							disabled={options.length === 2}
              >
							Delete
						</button>
					</div>
            { !options[index].value && <small className="mt-0 d-block">{errors.options && errors.options[index]}</small> }
        </div>
				))}
				<button type="submit" id="submit" className="btn btn-primary btn-sm mt-1">
					{this.props.buttonValue}
        </button>
        {this.state.isLoading && <Loading /> }
			</form>
		);
	}
}

export default FormModal;
