import React, { Component } from 'react';
import FormModal from './FormModal';

export default class LeftSidebar extends Component {

	state = { modalOpen: false };
	toggleModal = () => this.setState({ modalOpen: !this.state.modalOpen });

	render() {
		const { polls } = this.props;
		return (
			<>
				<div className="form-group d-flex justify-content-between">
					<input onChange={this.props.setSearchTerm} value={this.props.searchTerm} type="text" placeholder="Search.." className="form-control form-control-sm" />
					<button onClick={this.toggleModal} className="btn btn-primary btn-sm ml-1">
						Create<span className="text-primary">_</span>Poll
					</button>
				</div>

				{this.props.polls.length > 0 ? (
					<div className="p-3 bg-secondary">
						<p className="badge badge-dark">Polls Lists</p>
						{polls.map((poll) => (
							<button
								onClick={() => this.props.setSelected(poll.id)}
								key={poll.id}
								className="btn btn-light btn-sm mt-1 btn-block text-justify"
							>
								<small>{poll.title.length > 25 ? poll.title.substr(0, 25) + '...' : poll.title}</small>
							</button>
						))}
					</div>
				) : (
					<h5 className="text-info">
						There is no poll! <small className="d-block text-warning">create poll!</small>
					</h5>
				)}

				<div style={{background:'#00000099'}} className={`text-dark modal ${this.state.modalOpen && 'd-block'}`}>
					<div className="modal-dialog">
						<div className="modal-content">
							<div className="modal-header py-1">
								<h5 className="modal-title text-primary">Create New Poll</h5>
								<button onClick={this.toggleModal} className="btn btn-danger btn-sm px-3">&times;</button>
							</div>
							<div className="modal-body">
								<FormModal toggleModal={this.toggleModal} buttonValue="Create Poll" submit={this.props.createPoll} />
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}
}
